<template>
  <div>
    <shop-header :headerTitle="headerTitle"/>
    <van-cell-group class="input-field">
      <van-field
        v-model="username"
        clearable
        label=""
        left-icon="contact"
        placeholder=""
      />

      <van-field
        v-model="password"
        clearable
        left-icon="contact"
        type="password"
        label=""
        placeholder=""
      >
        <span slot="left-icon" class="iconfont">&#xe672;</span>
      </van-field>
    </van-cell-group>
    <div class="button-area">
      <van-button
        type="primary"
        class="login-button"
        @click="login"
      ></van-button>
      <van-button
        plain
        type="primary"
        class="login-button"
      ></van-button>
    </div>
  </div>
</template>
<script>
import { Field, CellGroup, Button, Toast } from "vant";
import ShopHeader from "../components/ShopHeader";
import { mapMutations } from "vuex";
export default {
  name: "Login",
  components: {
    ShopHeader,
    [Field.name]: Field,
    [CellGroup.name]: CellGroup,
    [Button.name]: Button,
    [Toast.name]: Toast
  },
  data() {
    return {
      headerTitle: "",
      username: "",
      password: ""
    };
  },
  methods: {
    login() {
      if (this.username == null || this.username == "") {
        this.$toast("!");
      } else if (this.password == null || this.password == "") {
        this.$toast("!");
      } else {
        this.http
          .post("/users/logincheck", {
            username: this.username,
            password: this.password
          })
          .then(Response => {
            let res = Response.data;
            if (res.code == "0") {
              this.setToken(res.msg.token);
              this.$router.replace("/user");
            } else {
              this.$toast("!");
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    ...mapMutations(["setToken"])
  }
};
</script>
<style lang='stylus' scoped>
.input-field
  margin-top: 30%
  margin-bottom: 10%
.button-area
  display: flex
  flex: 1
  flex-flow: column wrap
  justify-content: space-around
  align-items: center
  .login-button
    width: 50%
    margin-bottom: 5%
</style>