<template>
  <div class="detail-sku">
    <!--  -->
    <van-cell-group class="cell-group">
      <van-cell
        :title="skuname"
        label=""
        icon="passed"
        @click="onSku"
      >
        <van-icon
          slot="right-icon"
          name="ellipsis"
          class="right-icon"
        />
      </van-cell>
    </van-cell-group>

    <van-sku
      v-model="showBase"
      :sku="sku"
      :goods="goods"
      :goods-id="gid"
      :close-on-click-overlay="closeOnClickOverlay"
      :initial-sku="initialSku"
      :hide-stock="hide_stock"
      @stepper-change="saveNum"
      @add-cart="addCart"
      @buy-clicked="onBuy"
    ></van-sku>

    <van-cell
      :title="address"
      label=""
      icon="location-o"
      @click="onAddress"
      title-class="address"
    ></van-cell>
  </div>
</template>
<script>
import { Cell, CellGroup, Icon, Sku, Dialog, Stepper, Toast } from "vant";
import { mapState, mapMutations } from "vuex";
export default {
  name: "DetailList",
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Icon.name]: Icon,
    [Sku.name]: Sku,
    [Stepper.name]: Stepper
  },
  data() {
    return {
      showBase: false,
      closeOnClickOverlay: true,
      hide_stock: true
    };
  },
  props: {
    imgurl: String,
    gid: String,
    price: Number,
    skuname: String
  },
  computed: {
    ...mapState(["address", "token", "Num"]),
    sku() {
      return {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            v: [
              {
                id: this.gid, // skuValueId id
                name: this.skuname, // skuValueName
                imgUrl: this.imgurl // 
              }
            ],
            k_s: "s1" // skuKeyStrsku  list key value  id
          }
        ],
        //  sku M  sku S 
        list: [
          {
            id: this.gid, // skuId
            price: this.price * 100, // 
            s1: this.gid, //  k_s  s1  id
            stock_num: 666
          }
        ],
        price: this.price + ".00", // 
        stock_num: 666
      };
    },
    goods() {
      return {
        // 
        title: this.skuname,
        //  sku 
        picture: this.imgurl
      };
    },
    initialSku() {
      return {
        // skuKeyStrsku  key 
        // skuValueId id
        s1: this.gid,
        // 
        selectedNum: 1
      };
    }
  },
  methods: {
    ...mapMutations(["setNum"]),
    onBuy(skuData) {
      this.$toast.success(",Id:" + skuData.goodsId);
    },
    addCart(skuData) {
      let newSkuData = {};
      newSkuData.goodsId = skuData.goodsId;
      newSkuData.num = skuData.selectedNum;
      newSkuData.price = skuData.selectedSkuComb.price;
      newSkuData.skuname = this.skuname;
      newSkuData.imgurl = this.imgurl;
      if (this.token) {
        this.http
          .post("/carts/add", {
            skuData: newSkuData
          })
          .then(Response => {
            let res = Response.data;
            if (res.code != "0") {
              this.$dialog
                .confirm({
                  title: "",
                  message: ""
                })
                .then(() => {
                  this.$router.replace("/login");
                })
                .catch(() => {});
            } else {
              this.showBase = false;
              this.$toast.success("");
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$dialog
          .confirm({
            title: "",
            message: ""
          })
          .then(() => {
            this.$router.replace("/login");
          })
          .catch(() => {});
      }
    },
    onSku() {
      this.showBase = !this.showBase;
    },
    saveNum(num) {
      this.setNum(num);
    },
    onAddress() {
      if (this.token) {
        this.$router.push("/addressList");
      } else {
        this.$dialog
          .confirm({
            title: "",
            message: ""
          })
          .then(() => {
            this.$router.replace("/login");
          })
          .catch(() => {});
      }
    }
  }
};
</script>
<style lang='stylus' scoped>
.cell-group
  padding-top: 0.8rem
  padding-bottom: 0.8rem
  background-color: #f5f5f5
.address
  overflow: hidden
  text-overflow: ellipsis
  white-space: nowrap
  .right-icon
    font-size: 25px
</style>