<template>
  <div>
    <van-sku
      v-model="showBase"
      :sku="sku"
      :goods="goods"
      :goods-id="gid"
      :close-on-click-overlay="closeOnClickOverlay"
      :initial-sku="initialSku"
      :hide-stock="hide_stock"
      @stepper-change="saveNum"
      ref="sku"
    >
      <van-button
        slot="sku-actions"
        type="primary"
        bottom-action
        @click="addCart"
      ></van-button>
    </van-sku>
    <van-goods-action>
      <van-goods-action-mini-btn
        icon="cart-o"
        text=""
        :info="infoNum"
        ref="ok"
        @click="goShopping"
      />
      <van-goods-action-big-btn
        @click="showSku"
        text=""
      />
      <van-goods-action-big-btn
        @click="onBuy"
        primary
        text=""
      />
    </van-goods-action>
  </div>
</template>
<script>
import {
  GoodsAction,
  GoodsActionBigBtn,
  GoodsActionMiniBtn,
  Sku,
  Button,
  Dialog,
  Toast
} from "vant";
import { mapState, mapMutations } from "vuex";
export default {
  name: "DetailAction",
  components: {
    [GoodsAction.name]: GoodsAction,
    [GoodsActionBigBtn.name]: GoodsActionBigBtn,
    [GoodsActionMiniBtn.name]: GoodsActionMiniBtn,
    [Sku.name]: Sku,
    [Button.name]: Button,
    [Dialog.name]: Dialog,
    [Toast.name]: Toast
  },
  props: {
    imgurl: String,
    gid: String,
    price: Number,
    skuname: String
  },
  computed: {
    ...mapState(["Num", "token", "cartsList"]),
    sku() {
      return {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            v: [
              {
                id: this.gid, // skuValueId id
                name: this.skuname, // skuValueName
                imgUrl: this.imgurl // 
              }
            ],
            k_s: "s1" // skuKeyStrsku  list key value  id
          }
        ],
        //  sku M  sku S 
        list: [
          {
            id: this.gid, // skuId
            price: this.price * 100, // 
            s1: this.gid, //  k_s  s1  id
            stock_num: 666
          }
        ],
        price: this.price + ".00", // 
        stock_num: 666
      };
    },
    goods() {
      return {
        // 
        title: this.skuname,
        //  sku 
        picture: this.imgurl
      };
    },
    initialSku() {
      return {
        // skuKeyStrsku  key 
        // skuValueId id
        s1: this.gid,
        // 
        selectedNum: this.Num
      };
    }
  },
  data() {
    return {
      showBase: false,
      closeOnClickOverlay: true,
      hide_stock: true,
      infoNum: 0
    };
  },
  mounted() {
    this.infoNum = this.cartsList.length;
  },
  methods: {
    showSku() {
      this.showBase = !this.showBase;
    },
    onBuy() {
      this.$toast.success(
        ",Id:" + this.$refs.sku.getSkuData().goodsId
      );
    },
    addCart() {
      let newSkuData = {};
      newSkuData.goodsId = this.$refs.sku.getSkuData().goodsId;
      newSkuData.num = this.$refs.sku.getSkuData().selectedNum;
      newSkuData.price = this.$refs.sku.getSkuData().selectedSkuComb.price;
      newSkuData.skuname = this.skuname;
      newSkuData.imgurl = this.imgurl;
      if (this.token) {
        this.http
          .post("/carts/add", {
            skuData: newSkuData
          })
          .then(Response => {
            let res = Response.data;
            if (res.code != "0") {
              this.$dialog
                .confirm({
                  title: "",
                  message: ""
                })
                .then(() => {
                  this.$router.replace("/login");
                })
                .catch(() => {});
            } else {
              this.showBase = false;
              this.$toast.success("");
              if (res.msg) {
                this.infoNum = this.infoNum + 1;
              }
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$dialog
          .confirm({
            title: "",
            message: ""
          })
          .then(() => {
            this.$router.replace("/login");
          })
          .catch(() => {});
      }
    },
    ...mapMutations(["setNum"]),
    saveNum(num) {
      this.setNum(num);
    },
    goShopping() {
      this.$router.push("/cart");
    }
  }
};
</script>
<style lang='stylus' scoped></style>